/* eslint-disable react/prop-types */

import React from 'react'
import { Link } from 'react-router-dom'
import { link } from './OrganizationMetrics.scss'

const OrganizationMetrics = ({ organizationId, metrics }) => {
  const { published, notPublishedYet, publishedByOthers } = metrics

  return (
    <div>
      <p><strong>{published}</strong> jeux de donnes sont <strong>publis et accessibles</strong> sur <a href='https://data.gouv.fr'>data.gouv.fr</a></p>
      <p><strong>{notPublishedYet}</strong> jeux de donnes sont <strong>en attente de publication</strong></p>
      <p><strong>{publishedByOthers}</strong> jeux de donnes sont <strong>publis par d'autres producteurs</strong></p>
      <Link className={link} to={`/publication/${organizationId}/datasets`}>Publier des donnes</Link>
    </div>
  )
}

export default OrganizationMetrics



